.App {
  height: 100vh;
  width: 100vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  font-size: 1em;
  color: #c0392b;
  align-items: center;
}

.App-logo {
  width: 5em;
  margin: 0;
  pointer-events: none;
  z-index: 2;
  position: relative;
  background-color: #fff;
}

.bandeau {
  display: block;
  width: auto;
  height: 10px;
  z-index: 0;
  background-color: #c0392b;
  top: 51px;
  position: relative;
}

.main-title {
  width: 100%;
  box-shadow: 0 0px 5px 0px rgba(0, 0, 0, 0.1);

  border-radius: 0 0 50px 50px;
  background-color: #fff;
  font-size: 1.5em;
  padding-top: 1em;
}

.main-container {
  padding: 0em 3em;
}
.main-view {
  display: flex;
  justify-content: space-evenly;
  width: 90%;
}
.sidebar {
  width: fit-content;
  height: fit-content;
  position: -webkit-sticky;
  position: sticky;
  top: 10em;
  min-width: 515px;
}

.content-container {
  display: block;
  height: 100%;
}

.col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.margin-bottom {
  margin-bottom: 3%;
}
.presview {
  margin: 1%;
  width: 70%;
}

#bandeau {
  background: #fd0;
  display: block;
  width: 100%;
  height: 15px;
  text-align: center;
  position: relative;
  top: 50;
}

button {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  border-radius: 3px;
  padding: 0.3rem 1rem;
  margin-left: 0.5rem;
}

.button-default {
  background: #c0392b;
  color: #fff;
}

button[type="submit"] {
  background: #205123;
  color: #fff;
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  padding: 0.7rem;
  border-radius: 50px 15px;
}
input,
textarea {
  margin: 2% 0;
  font-size: 1em;
}

textarea {
  padding: 0.5rem 0.5rem;
  margin: 5%;
  width: 70%;
}

label {
  font-size: 1em;
  color: gray;
  margin: 0 10%;
}

form {
  text-align: center;
}
input[type="button"] {
  background-color: #c0392b;
  border: none;
  color: #fff;
  font-size: 0.9rem;
  font-weight: 700;
  border-radius: 3px;
  padding: 0.3rem 1rem;
  margin: 5%;
}
input {
  width: 70%;
}

input[type="number"] {
  font-weight: 700;
  color: #c0392b;
  width: 30%;
  padding: 10px;
}

.desc-text {
  text-align: justify;
  padding: 0px 20px;
}
.row > input {
  margin: 10px;
}

.features-small-item {
  background: #ffffff;
  box-shadow: 0 0px 50px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  text-align: center;
}
.invalid_menu {
  font-weight: bold;
  color: #fa755a;
}

nav > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-item > li {
  display: block;
  font-size: 14px;
  margin: 3% 0;
  padding: 4px;
  border-radius: 25px 7.5px;
}

.main-nav > li {
  display: block;
  font-size: 16px;
  margin: 1%;
  padding: 10px;
  border-radius: 50px 15px;
}

.food_sumup {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.food_sumup_list {
  padding: 0;
  color: gray;
  border: 1.5px dashed;
  width: 60%;
}
.food_sumup_list > li {
  padding: 8px 0px;
  background-color: transparent;
  text-align: center;
}

li {
  display: block;
  margin: 1% 0;
  background-color: #c0392b;
  padding: 15px;
  border-radius: 50px 15px;
}
a {
  padding: 15px;
  color: #fff;
  text-decoration: none;
}

.bg-black {
  background-color: #000;
}
.scrollable {
  width: 100%;
  overflow: scroll;
}

@media screen and (max-width: 1200px){
  .App {
    overflow: scroll;
    flex-direction: column;
  }
  .main-view {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main-container {
    padding: 0;
  }
  
  .margin-bottom {
    margin-bottom: 5%;
  }
  .sidebar {
    width: 90%;
  }
  
  .presview {
    margin: 3%;
  }
  .StripeElement {
    width: 70%;
    margin: 5px;
    padding: 11px 15px 11px 0;
  }
}